import { Point, Rect } from "../utils.js"

export default class Quadtree {
  /** @type {Quadtree} */
  northeast = null
  /** @type {Quadtree} */
  northwest = null
  /** @type {Quadtree} */
  southeast = null
  /** @type {Quadtree} */
  southwest = null

  references = []
  divided = false

  /**
   * @param {Rect} boundary
   * @param {number} resolution
   */
  constructor( boundary, approximateResolution=2 ) {
    this.boundary = boundary
    this.averageLength = (boundary.width + boundary.height) / 2
    this.maxDeph = Math.round( Math.log2( this.averageLength / approximateResolution ) )
    this.smallestBoundary = new Rect( 0, 0, boundary.width / (2 ** this.maxDeph), boundary.height / (2 ** this.maxDeph) )
    this.resolution = approximateResolution//(boundary.width > boundary.height ? boundary.width : boundary.height) / (2 ** this.maxDeph)
  }

  /**
   * @param {Point[]} vertices
   */
  insertPolygon( object, ...vertices ) {
    if (vertices.length < 3) return

    vertices.push( vertices[ 0 ] )

    this.insertPointSequence( object, ...vertices )

    const point1 = vertices[ 0 ]
    const point2 = vertices[ 1 ]
    const pointNearFirstLine = new Point(
      point1.x + (point2.x - point1.x) / 2 + (point1.y !== point2.y ? this.resolution : 0),
      point1.y + (point2.y - point1.y) / 2 + (point1.y === point2.y ? this.resolution : 0),
    )

    const maxX = Math.max( ...vertices.map( v => v.x ) )
    const rect = new Rect( pointNearFirstLine.x, pointNearFirstLine.y, maxX - pointNearFirstLine.x, 1 )
    const numberOfEdgesCrossing = this.queryLeaves( rect ).filter( (qTree, i, arr) => {
      if (qTree.references.length === 0) return false

      const nextTree = arr[ i + 1 ]

      return !nextTree || qTree.boundary.x + this.resolution !== nextTree.boundary.x

    } ).length

    if (numberOfEdgesCrossing % 2 === 1) this.fillEmptyArea( object, pointNearFirstLine )
  }

  fillEmptyArea( object, point, direction=null ) {
    if (!this.divided) {

      if (this.references.find( ref => ref === object )) return { left:false, right:false, top:false, bottom:false }

      this.references.push( object )

      return { left:true, right:true, top:true, bottom:true }

      // sprawdzam czy zajty czy nie
      // jeli nie, to wypeniam referencj i zwracam tablic punktw wskazujcych na kadego ssiada
      // w przeciwnym razie zwracam pust tablic

    } else {
      // const siblingstoTest = this.fillEmptyArea( object, point )

      // iteruj punkty i sprawdzam pasujace potomki
      // otrzymane tablice filtruj wzgldem pooenia potomka w strukturze
      // np pnocne nie mog wskaza na poudnie (bo w strukturze maj na poudniu ssiada)
      //    wschodnie nie moga wskaza na zachodnie

      // po otrzymaniu wszystkich odpowiedni i przefiltrowaniu tablicy wysyam j do rodzica na dalsze sprawdzanie
    }
  }

  /**
   * @param {Point[]} points
   */
  insertPointSequence( object, ...points ) {
    if (!points.length) return

    if (points.length === 1) this.insert( object, points[ 0 ] )
    else for (let i = 1; i < points.length; i++) {
      const pointA = points[ i - 1 ]
      const pointB = points[ i     ]

      Quadtree.bresenham( pointA, pointB, this.smallestBoundary.width, this.smallestBoundary.height ).forEach( point => this.insert( object, point ) )
    }
  }

  /**
   * @param {Point} point
   */
  insert( object, point ) {
    if (!this.boundary.contains( point )) return false

    const { references, divided, boundary, smallestBoundary } = this

    if (boundary.width <= smallestBoundary.width) {
      if (!references.includes( object )) references.push( object )

      return true
    } else {
      if (!divided) this.subdivide()

      return this.northeast.insert( object, point )
        || this.northwest.insert( object, point )
        || this.southeast.insert( object, point )
        || this.southwest.insert( object, point )
    }
  }

  subdivide() {
    const { x, y, width, height } = this.boundary
    const { resolution } = this

    const ne = new Rect( x + width / 2, y,              width / 2, height / 2 )
    const nw = new Rect( x,             y,              width / 2, height / 2 )
    const se = new Rect( x + width / 2, y + height / 2, width / 2, height / 2 )
    const sw = new Rect( x,             y + height / 2, width / 2, height / 2 )

    this.northeast = new Quadtree( ne, resolution )
    this.northwest = new Quadtree( nw, resolution )
    this.southeast = new Quadtree( se, resolution )
    this.southwest = new Quadtree( sw, resolution )

    this.divided = true

    this.references.forEach( point => this.insert( point ) )
    this.references = []
  }

  /**
   * @param {Rect} rect
   */
  queryLeaves( rect ) {
    /** @type {Quadtree[]} */
    const foundedLeaves = []
    const { boundary } = this

    if (!this.boundary.intersects( rect )) return foundedLeaves

    const { divided, northeast, northwest, southeast, southwest, resolution } = this

    if (rect.intersects( new Rect( boundary.x, boundary.y, resolution, resolution ) )) foundedLeaves.push( this )

    if (divided) foundedLeaves.push(
      ...northeast.queryLeaves( rect ),
      ...northwest.queryLeaves( rect ),
      ...southeast.queryLeaves( rect ),
      ...southwest.queryLeaves( rect ),
    )

    return foundedLeaves
  }

  query( rect ) {
    return this.queryLeaves( rect )
      .map( qTree => qTree.references )
      .filter( (reference, i, arr) => arr.indexOf( reference ) === i )
  }

  /**
   *
   * @param {CanvasRenderingContext2D} ctx
   * @param {object} param1
   * @param {number} param1.drawAreaX
   * @param {number} param1.drawAreaY
   * @param {Rect} param1.drawArea
   */
  show( ctx, { isMeshShowed=true, drawAreaX=0, drawAreaY=0, drawArea } ) {
    const { x, y, width, height } = this.boundary
    const { references:points } = this

    ctx.strokeStyle = `#aaa`
    ctx.lineWidth = 1

    if (drawArea) {
      drawAreaX = drawArea.x
      drawAreaY = drawArea.y
    }

    if (isMeshShowed) ctx.strokeRect( drawAreaX + x, drawAreaY + y, width, height )

    if (this.divided) {
      this.northeast.show( ctx, { isMeshShowed, drawAreaX, drawAreaY } )
      this.northwest.show( ctx, { isMeshShowed, drawAreaX, drawAreaY } )
      this.southeast.show( ctx, { isMeshShowed, drawAreaX, drawAreaY } )
      this.southwest.show( ctx, { isMeshShowed, drawAreaX, drawAreaY } )
    }

    ctx.fillStyle = '#0b0'

    if (points.length) {
      ctx.fillRect( ( drawAreaX + x ), ( drawAreaY + y ), ( width ), ( height ) )
    }
  }

  clear() {
    this.references.splice( 0 )

    if (this.divided) {
      this.northeast = null
      this.northwest = null
      this.southeast = null
      this.southwest = null

      this.divided = false
    }
  }

  static bresenham( { x:xA, y:yA }, { x:xB, y:yB }, resolutionX=1, resolutionY=resolutionX ) {
    const coordinates = []

    const minRes = Math.min( resolutionX, resolutionY )

    let x1 = this.floorToDivisible( xA, minRes )
    let y1 = this.floorToDivisible( yA, minRes )
    const x2 = this.floorToDivisible( xB, minRes )
    const y2 = this.floorToDivisible( yB, minRes )

    const deltaX = Math.abs( x1 - x2 )
    const deltaY = Math.abs( y1 - y2 )

    const stepX = (x1 < x2 ? 1 : -1) * minRes
    const stepY = (y1 < y2 ? 1 : -1) * minRes

    let err = deltaX - deltaY

    do {
      coordinates.push( new Point( x1 + minRes / 2, y1 + minRes / 2 ) )

      const doubledErr = err * 2

      if (doubledErr > -deltaY) {
        err -= deltaY
        if (x1 !== x2) x1 += stepX
      }
      if (doubledErr < deltaX) {
        err += deltaX
        if (y1 !== y2) y1 += stepY
      }
    } while (x1 !== x2 || y1 !== y2)

    coordinates.push( new Point( x1 + minRes / 2, y1 + minRes / 2 ) )

    return coordinates
  }

  static floorToDivisible( number, divider ) {
    return Math.floor( number / divider ) * divider
  }
}